


* {
  color: white;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
}

.AuthScreen {
  display: flex;
  justify-content: center;
  align-items: center;

}

.auth-box {
  margin-top: 3cm;
  width: 17cm;
  // border: 1px solid white;
  border-radius: 10px;
  background-color: #0f0f13;
  padding: .3cm .6cm;
}

.welcome {
 margin-bottom: -.45cm;
}

.tagline {
  font-size: 65px;
  line-height: 1.75cm;
  // font-weight: 600;
  color: rgb(243, 243, 243);
  font-family:Tilt Neon;
}

.sub-tagline {
  font-size: 25px;
  margin-top: .3cm;
  color: rgb(96, 96, 96);
  font-family:Tilt Neon;
}

.google-button{
  margin-top: 1.3cm;
  margin-bottom: .5cm;
  background-color: #2d2d2d;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family:Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover{
    background-color: transparent;
  }
}